<template>
    <div class="content-wrapper">
        <section class="content">
            <div class="container-fluid">
                <div class="container">
                    <div class="def_home">
                        <div class="app-page">
                            <div class="head">
                                <h3>Add Username</h3>
                            </div>
                            <div class="blog">
                                <div class="card card-info">
                                    <div class="card-body">
                                        <form class="form-horizontal">
                                            <div class="card-body">
                                                <div class="usernew_name card-bod">
                                                    <label class="input-group-text fname">{{user.input[0].txt}}</label>
                                                    <input class="form-control" type="text"
                                                        v-model="user.input[0].username">
                                                </div>
                                                <div class="usernew_pass card-bod">
                                                    <label class="input-group-text fname">{{user.input[1].txt}}</label>
                                                    <input class="form-control" type="text"
                                                        v-model="user.input[1].passwords">
                                                    <div>


                                                        <a class="btnns"
                                                            @click="user.input[1].passwords=randomPass()">Generate
                                                            random</a>
                                                    </div>
                                                </div>
                                                <div class="usernew_mail card-bod">
                                                    <label class="input-group-text fname">{{user.input[2].txt}}</label>
                                                    <input class="form-control" type="text"
                                                        v-model="user.input[2].mails">
                                                </div>
                                                <div class="usernew_icon card-bod">
                                                    <label class="input-group-text fname">{{user.input[3].txt}}</label>
                                                    <input class="form-control" type="text"
                                                        v-model="user.input[3].icon">
                                                    <label for="userfileIcon"><span class="btnns">تحميل
                                                            صورة<i></i></span></label>
                                                    <input name="userfileIcon" style="display:none" type="file"
                                                        @change="uploadImage()" id="userfileIcon">
                                                </div>
                                                <div class="usernew_sex card-bod">
                                                    <label class="input-group-text fname">{{user.input[4].txt}}</label>
                                                    <div>
                                                        <li><input name="sex" value="male" type="radio"
                                                                v-model="user.input[4].sex">male</li>
                                                        <li><input name="sex" value="female" type="radio"
                                                                v-model="user.input[4].sex">female</li>
                                                    </div>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div @click="saveDate()" class="btns">
                <input value="Save" type="button" class="btnn">
            </div>
        </section>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                user: {
                    input: [{
                            username: "",
                            txt: "Nick"
                        },
                        {
                            passwords: "",
                            txt: "Password"
                        },
                        {
                            mails: "",
                            txt: "Email"
                        },
                        {
                            icon: "",
                            txt: "Profile images"
                        },
                        {
                            sex: "",
                            txt: "Gender"
                        },

                    ]
                }
            }
        },
        methods: {

            randomPass() {
                var generatePassword = (
                        length = 10,
                        wishlist = "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz~!@-#$"
                    ) => Array(length)
                    .fill('')
                    .map(() => wishlist[Math.floor(crypto.getRandomValues(new Uint32Array(1))[0] / (0xffffffff + 1) *
                        wishlist.length)])
                    .join('');

                return generatePassword();
            },
            uploadImage() {

            },
        }
    }
</script>
