    .card .usernew_sex li input {
        margin: 10px 6px;

    }

    .card .usernew_sex li {
        display: inline-block;
        margin: 10px 20px;

    }

    .usernew_icon label,
    .usernew_sex div,
    .usernew_pass div {
        width: 100%;
        background: #f5e9e9;
        padding: 10px;
    }

    .btnns {
        background: #02a9eb;
        color: white !important;
        ;
        border: none;
        font-family: 'Almarai', sans-serif !important;
        margin: 0;
        padding: 5px 10px;
        width: 170px;
        text-align: center;
    }

    .btnn {
        background: #0085ba;
        color: white !important;
        ;
        border: none;
        font-family: 'Almarai', sans-serif !important;
        margin: 0;
        padding: 5px 10px;
        width: 170px;
        text-align: center;
    }

    .usernew_sex div {
